					<view class="wx_li">
						<i>*</i>
						<text>下车站点:</text>
						<input type="text" v-model="outSite" placeholder="请输入下车站点" />
					</view>
					<view class="wx_li">
						<text>驾驶员:</text>
						<input type="text" v-model="driver" placeholder="请输入驾驶员名字" />
					</view>
					<view class="wx_li">
						<text>乘务员:</text>
						<input type="text" v-model="crewMember" placeholder="请输入乘务员名字" />
					</view>
					<view class="wx_li">
						<text>深圳通卡号:</text>
						<input type="text" v-model="cardNumber" placeholder="请输入深圳通卡号" />
					</view>
					<view class="wx_li">
						<i>*</i>
						<text>是否答复:</text>
						<view class="model" v-model="whetherReply">
							<picker class="model_on" @change="bindPickerChange" :value="index" :range="array">
								<view class="uni-input">{{ array[index] }}</view>
							</picker>
						</view>
					</view>
					<view class="wx_content">
						<text>信访内容:</text>
						<textarea v-model="content" minlength="30" placeholder="信访内容或者提出的建议不少于30个字"/>
					</view>
				</view>
				<!-- <view class="uploade_wrap">
					图片上传
					<view class="wxImg" @tap="chooseImages">
						<image src="../../static/up.png" mode=""></image>
						<text class="imgBox" @tap="chooseImages"></text>
					</view>
					图片上传
					<view class="wxImg" @tap="chooseImages">
						<image src="../../static/up.png" mode=""></image>
						<text class="imgBox" @tap="chooseImages"></text>
					</view>
					图片上传
					<view class="wxImg" @tap="chooseImages">
						<image src="../../static/video.png" mode=""></image>
						<text class="imgBox" @tap="chooseImages"></text>
					</view>
				</view> -->
				<view class="wx_submit"><button type="grey" @tap="submit()">确认提交</button></view>
				<!-- <view class="wx_submit"><button type="primary" @tap="modelSubmit">确认提交</button></view> -->
				<!-- 弹框1 -->
				<view class="wx_model" v-if="show">
					<view class="wx_model_text">您的信访流水号为C18090100001,我们将在10个工作日内联系您,请保持电话畅通,有任何疑问,欢迎拨打我们的服务热线83161116反映,谢谢!</view>
					<view class="wx_model_btn">
						<view class="wx_model_btn_left" @tap="modelSubmit()">确认</view>
					</view>
				</view>
				<!-- 弹框2 -->
				<view class="wx_model" v-if="isShow">
					<view class="wx_model_text">虽然您不需要答复,但为了到位处理您反映的问题,以改进不足,我们可能有需要联系您,以补充完善相关信息,有任何疑问,欢迎拨打我们的服务热线83161116反映,谢谢!</view>
					<view class="wx_model_btn">
						<view class="wx_model_btn_left" @tap="modelSubmit()">确认</view>
					</view>
				</view>
				<!-- 弹框3 -->
				<view class="wx_model" v-if="nowShow">
					<view class="wx_model_text" style="font-size: 16px;">“请确保信访内容详实有效,且与巴士集团服务业务相关,否则有可能不予回复,详情可咨询83161116”。</view>
					<view class="wx_model_text" style="color: #FF3c3c;">注(带*的为必填项，如果存在“无”的输入内容时要删除“无”的字样在输入！！)</view>
					<view class="wx_model_btn">
						<view class="wx_model_btn_left" @tap="modelSubmit()">确认</view>
					</view>
				</view>
				<!-- 个人工单列表中心 -->
				<view class="man" @tap="man()">
					<image src="../../static/icon/man.png" mode=""></image>
				</view>
			</view>
			<view class="wx_bottom">有任何疑问，欢迎拨打我们的服务热线83161116！</view>
			<view class="" v-if="uuidShow">
				{{uuid}}
				<input type="hidden" v-model="uuid" value="" />
			</view>
		</view>
		<zlpicker
			v-model="zlpvalue"
			ref="zlpicker"
			:data="lines"
			:initSelected="initSelected"
			:multiple="false"
			:showSearch="true"
			@changeZLPvalue="changeZLPvalue"
			@confirm="confirmzlpvalue"
			@change="changezlpvalue"
			@input="inputzlpvalue"
		></zlpicker>
	</view>
