<script>
var user = uni.getStorageSync('user');
var id;
import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue';
import wPicker from '@/components/w-picker/w-picker.vue';
import zlpicker from '@/components/zhilin-picker/zhilin-picker.vue';
import jPicker from '@/components/J-Picker/jPicker.vue';
export default {
	components: { ruiDatePicker, wPicker, zlpicker, jPicker },
	data() {
		return {
			array: ['无', '是', '否'],
			index: 0,
			value: '',
			val: '',
			number: '',
			visiter: '',
			telephone: '',
			busNumber: ' 粤B',
			visitTime: '',
			eventTime: '',
			intoSite: '无',
			outSite: '无',
			driver: '无',
			crewMember: '无',
			whetherReply: '',
			cardNumber: '无',
			content: '',
			// 公交线路
			busLine:'',
			lines: [],
			keywords: '', //搜索关键词
			zlpvalue: false,
			initSelected: [],
			id: '',
			nData: {
				bookTypeVal: 0,
				manTypeVal: 0,
				zdTypeVal: 0
			},
			shaowBtn: true,
			// // 图片上传
			file1: '',
			file2: '',
			// // 视频上传
			file3: '',
			// 弹框是否答复
			show: false,
			isShow: false,
			nowShow: true,
			uuidShow: false,
			uuid: ''
		}

	},
	computed:{
		dateValue(){
			return '2020-03-15 10:45:00'
		}
	},
	onLoad() {
		this.init();
		this.getNumber();
		this.getLineSites();
	},
	methods: {
		init() {
			var uuid = localStorage.getItem("uuid");
			if(uuid==undefined || uuid == null || uuid==""){
				// this.uuid = Number(Math.random().toString().substr(3,length) + Date.now()).toString(36);
				this.uuid = new Date().getTime()
				localStorage.setItem("uuid",this.uuid);
			}
			this.uuid = uuid;
			//赋值到页面的uuid
			console.log(uuid, 123)
			
		},
		// 流水号
		getNumber() {
			var url = this.$url + 'workNumber';
			uni.request({
				url: url,
				data: {
					number: this.number,
				},
				method: 'GET',
				success: res => {
					console.log(res);
					this.number = res.data;
					this.total = res.total;
				}
			});
		},
		// 弹框
		modelSubmit() {
			this.show = false,
			this.isShow = false
			this.nowShow = false
		},
		// 公交线路
		searchInput(val) {},
		selectZLpicker() {
			this.zlpvalue = true;
			this.shaowBtn = false;
		},
		confirmzlpvalue(e) {
			console.log(e)
			this.busLine = e;
		},
		inputzlpvalue(e) {},
		changeZLPvalue(v) {},
		changezlpvalue (){},
		// 获取公交线路数据
		getLineSites() {
			var url = this.$url + 'getBusList';
			uni.request({
				url: url,
				data: {
					busLine: this.busLine,
				},
				method: 'GET',
				header: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					// console.log(res);
					this.lines = res.data.map(p => {
						// console.log(p)
						return {
							label: p,
							value: p
						};
					});
				},
				fail: (data, code) => {
					uni.showToast({
						icon: 'none',
						title: '網絡異常'
					});
					return false;
				}
			});
		},
		// 是否答复
		bindPickerChange: function(e) {
			console.log('picker发送选择改变，携带值为', e.target.value);
			this.index = e.target.value;
			this.whetherReply = e.detail.value;
			console.log(e.detail.value);
		},
		// 信访时间
		bindChangevisitTime(val) {
			this.val = val;
			this.visitTime = val;
			console.log('时间', val);
		},
		// 时间选择
		bindChange(value) {
			this.eventTime = value;
			console.log('时间', value);
		},
		// 获取数据
		getNews() {
			uni.showNavigationBarLoading();
			var url = this.$url + 'vxworkorder';
			let page = this.page;
			uni.request({
				url: url,
				data: {
					// 登录
					username: user.number,
					choice: this.choice,
					id: this.id,
					number: this.number,
					visiter: this.visiter,
					telephone: this.telephone,
					busLine: this.busLine,
					busNumber: this.busNumber,
					visitTime: this.visitTime,
					eventTime: this.eventTime,
					intoSite: this.intoSite,
					outSite: this.outSite,
					driver: this.driver,
					crewMember: this.crewMember,
					whetherReply: this.whetherReply,
					content: this.content,
					// file1: this.file1,
					// file2: this.file2,
					// file3: this.file3,
				},
				method: 'GET',
				success: res => {
					var rdata = res.data;
					this.total = rdata.total;
					uni.showToast({
						icon: 'none',
						title: res.data.msg
					});
					console.log(rows, 111111);
				},
				fail: (data, code) => {
					uni.showToast({
						icon: 'none',
						title: '網絡異常'
					});
					uni.stopPullDownRefresh();
					return false;
				}
			});
		},
		submit() {
			// 数字转文字
			if (this.whetherReply == 0) {
				this.whetherReply = '无';
			} else if (this.whetherReply == 1) {
				this.whetherReply = '是';
				this.show = true;
			} else if (this.whetherReply == 2) {
				this.whetherReply = '否';
				this.isShow = true;
			};
			if(!this.handlervalidate()){
				return false;
			}
				var url = this.$url + 'vxworkorder';
				// if(uuid==undefined || uuid == null || uuid==""){
				// 	this.uuid = new Date().getTime()
				// }
				uni.request({
					url: url,
					method: 'POST',
					header: {
						'Content-Type': 'application/x-www-form-urlencoded'
					},
					data: {
						number: 'C' + this.number,
						visiter: this.visiter,
						telephone: this.telephone,
						busLine: this.busLine,
						busNumber: this.busNumber,
						visitTime: this.visitTime,
						eventTime: this.eventTime,
						intoSite: this.intoSite,
						outSite: this.outSite,
						driver: this.driver,
						crewMember: this.crewMember,
						cardNumber: this.cardNumber,
						whetherReply: this.whetherReply,
						content: this.content,
						uuid: this.uuid,
						// file1: this.file1,
						// file2: this.file2,
						// file3: this.file3,
					},
					success: res => {
						console.log(res);
						uni.showToast({
							icon: 'none',
							title: '保存成功'
						});
						// 返回
						// setTimeout(() => {
						// 	uni.navigateBack({
						// 		delta: 1
						// 	});
						// }, 3000);
					},
					fail: () => {
						uni.showToast({
							title: '网络异常',
							icon: 'none'
						});
					}
				});
			
		},
		handlervalidate(){
			if(this.visiter==""){
				uni.showToast({
					icon: 'none',
					title: '信访人姓名不能为空'
				});
				return false;
			}
			if(this.telephone==""){
				uni.showToast({
					icon: 'none',
					title: '联系电话不能为空'
				});
				return false;
			}
			if(this.busLine==""){
				uni.showToast({
					icon: 'none',
					title: '公交线路不能为空'    
				});
				return false;
			}if(this.busNumber==""){
				uni.showToast({
					icon: 'none',
					title: '车辆编号不能为空'
				});
				return false;
			}
			if(this.eventTime==""){
				uni.showToast({
					icon: 'none',
					title: '事件发生的时间不能为空'
				});
				return false;
			}if(this.intoSite==""){
				uni.showToast({
					icon: 'none',
					title: '上车站点不能为空'
				});
				return false;
			}
			if(this.outSite==""){
				uni.showToast({
					icon: 'none',
					title: '下车站点不能为空'
				});
				return false;
			}
			if(this.whetherReply==""){
				uni.showToast({
					icon: 'none',
					title: '是否答复不能为空'
				});
				return false;
			}
			var minlength = 30;
			if( this.content.length < minlength) {
				uni.showToast({
					icon: 'none',
					title: '内容字数不足30'
				});
				return false;
			}
			return true;
		},
		// 图片上传
		chooseImages() {
			let _this = this;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'],
				success:function(res) {
					//因为有一张图片， 输出下标[0]， 直接输出地址
					var imgFiles = res.tempFilePaths[0]
					console.log(imgFiles);
					// 做成一个上传对象
					uni.uploadFile({
					    // 需要上传的地址
					    url:'',
						method:"POST",
						header:{
						    'Content-Type':'multipart/form-data'
						},
					    // filePath  需要上传的文件
					    filePath: imgFiles,
					    name: 'file',
					    success(res) {
					        // 显示上传信息
					        console.log(res)
					    }
					});
					
				}
			})
		},
		man() {
			uni.navigateTo({
				url: `/pages/chase/chase_next?uuid=${this.uuid}`
			});
			// let item = {
			// 	uuid: this.uuid,
			// 	// number: 'C' + this.number,
			// 	// visiter: this.visiter,
			// 	// telephone: this.telephone,
			// 	// busLine: this.busLine,
			// 	// busNumber: this.busNumber,
			// 	// visitTime: this.visitTime,
			// 	// eventTime: this.eventTime,
			// 	// intoSite: this.intoSite,
			// 	// outSite: this.outSite,
			// 	// driver: this.driver,
			// 	// crewMember: this.crewMember,
			// 	// cardNumber: this.cardNumber,
			// 	// whetherReply: this.whetherReply,
			// 	// content: this.content,
			// }
			// uni.setStorage({
			// 	key: 'item',
			// 	data: JSON.stringify(item),
			// 	success() {
			// 		uni.navigateTo({
			// 			url: `/pages/chase/chase_next?uuid=${this.uuid}`
			// 		});
			// 	}
			// })
			
		}
	}
};
</script>

